import React from "react"
import { Row, Col } from "react-bootstrap"

function WorkItem({ item, ...otherProps }) {
  const { logo, link, place, activities } = item
  return (
    <div className="work-item">
      <Row className="align-items-center">
        {logo ? (
          <Col md={4} className="text-center">
            <img
              src={`/images/logos/${logo}`}
              alt={place}
              className="img img-fluid"
            />
          </Col>
        ) : (
          <Col md={2} />
        )}
        <Col md={8}>
          {link ? (
            <h6 className="place">
              <a href={link} target="_blank" rel="noopener noreferrer">
                {place}
              </a>
            </h6>
          ) : (
            <h6 className="place">{place}</h6>
          )}
          {activities.map(({ title, description, dateRange }, i) => (
            <div className={i > 0 ? "pt-3" : ""}>
              <h4 className="title">
                {title} <span className="date">{dateRange}</span>
              </h4>
              {description && <p className="description mb-0">{description}</p>}
            </div>
          ))}
        </Col>
      </Row>
    </div>
  )
}
export default WorkItem
